import React, { useState, useEffect, useContext } from 'react';
import { useHistory } from 'react-router-dom';

import styles from './SignupModal.module.scss';

import { AuthContext } from '../../auth/Auth';
import { api } from './../../apis';
import Modal from '../Modal';
import Button from '../Button';
import Input from '../Input';

const SignupModal = ({ pageToggler }) => {
  const history = useHistory();

  const [userCredentials, setUserCredentials] = useState({
    firstName: '',
    lastName: '',
    email: '',
    password: '',
    confirmPassword: '',
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setUserCredentials({
      ...userCredentials,
      [name]: value,
    });
  };

  const onSignUp = async (e) => {
    e.preventDefault();
    const { firstName, lastName, email, password } = userCredentials;
    try {
      const res = await api.post('/users', { firstName, lastName, email, password });
      const token = res.headers['x-auth-token'];
      console.log(token);
      if (!token) {
        pageToggler();
      }
      localStorage.setItem('token', token);
      history.push('/profile');
      pageToggler();
    } catch (error) {
      console.log(error);
    }
  }

  useEffect(() => {
    console.log(userCredentials);
  }, [userCredentials]);

  return (
    <Modal onRequestClose={pageToggler} >
      <Modal.Header>Join us</Modal.Header>
      <Modal.Content>
        <form className={styles.container}>
          <div className={styles.input_wrapper} >
            <Input
              label="First Name"
              name="firstName"
              type="string"
              placeholder="First Name"
              required
              value={userCredentials.firstname}
              onChange={handleChange}
            />
          </div>
          <div className={styles.input_wrapper} >
            <Input
              label="Last Name"
              name="lastName"
              type="string"
              placeholder="Last Name"
              required
              value={userCredentials.lastname}
              onChange={handleChange}
            />
          </div>
          <div className={styles.input_wrapper} >
            <Input
              label="Email"
              name="email"
              type="email"
              placeholder="Email"
              required
              value={userCredentials.email}
              onChange={handleChange}
            />
          </div>
          <div className={styles.input_wrapper} >
            <Input
              label="Password"
              name="password"
              type="password"
              placeholder="Password"
              required
              value={userCredentials.password}
              onChange={handleChange}
            />
          </div>
          <div className={styles.input_wrapper} >
            <Input
              label="Confirm Password"
              name="confirmPassword"
              type="password"
              placeholder="Confirm Password"
              required
              value={userCredentials.confirmPassword}
              onChange={handleChange}
            />
          </div>
          <Button type="submit" onClick={onSignUp}>Sign up</Button>
        </form>
      </Modal.Content>
    </Modal>
  );
};

export default SignupModal;
